import React from "react";
import {CardContent} from "./CardContent/CardContent";
import './Card.css'
import './RtlCard.css'
import {RightTriangle} from "./triangles/RightTriangle";
import {LeftTriangle} from "./triangles/LeftTriangle";
import {BottomLeftTriangle} from "./triangles/BottomLeftTriangle";
import {BottomRightTriangle} from "./triangles/BottomRightTriangle";


export const RtlCard = (props) => {
    const className = "card " + props.cardId + " card-rtl" + ((props.last) ? " last-card" : "");
    return (
        <div className={className}>
            <RightTriangle/>
            {props.children}
            {props.last && <BottomRightTriangle/>}
        </div>
    )
}