import React from 'react';
import './Demo.css';
import TitleContainer from "../components/Title/TitleContainer";
import {Card} from "../components/Card/Card";
import {RtlCard} from "../components/Card/RtlCard";
import {Footer} from "../components/Footer/Footer";
import {IconsContainer} from "../components/Title/IconContainer/IconsContainer";
import {CardContent} from "../components/Card/CardContent/CardContent";
import {members} from "../members";

const Demo = () => {
    const CardType = (props) => props.rtl ? (<RtlCard {...props} />) : (<Card {...props} />);

    const cards = members.map((item, index) => (
        <CardType cardId={"card" + (index + 1).toString()} last={index === members.length - 1} rtl={index % 2 === 0}>
            <CardContent nick={item.nick} name={item.name} email={item.email} desc={item.desc}/>
        </CardType>
    ))

    return (
      <React.Fragment>
          <div id="background"/>
          <TitleContainer className='demo' title={"This is our team"} desc={"We are a young and energetic team of enthusiastic students who believe in innovative strategies and out-of-the-box solutions. We are always motivated to find ways that make things work differently. This year we are finishing studying at the University of Warsaw and we would like to complete our degrees in the challenging and inspiring way. We are hard-working and always strive to do our best. You won't be disappointed by choosing us!"}>
              <IconsContainer names={['team', 'student', 'computer']} />
          </TitleContainer>

          {cards}

          <Footer/>
      </React.Fragment>
  )
};

export default Demo;
