import React from 'react';
import '../MainPage/Demo.css';
import TitleContainer from "../components/Title/TitleContainer";
import {Card} from "../components/Card/Card";
import {RtlCard} from "../components/Card/RtlCard";
import {Footer} from "../components/Footer/Footer";
import {PhotoContainer} from "../components/Title/PhotoContainer/PhotoContainer";
import {CardContent} from "../components/Card/CardContent/CardContent";
import {ProjGallery} from "../components/Card/CardProject/ProjGallery";
import { useLocation } from "react-router-dom";
import Demo from '../MainPage/Demo';
import {members} from "../members";
import {IconsContainer} from "../components/Title/IconContainer/IconsContainer";

const Personal = () => {
    const nick = useLocation().pathname.substring(1)
    const person = members.filter((item) => item.nick === nick)[0]

    const CardType = (props) => props.rtl ? (<RtlCard {...props} />) : (<Card {...props} />);
    return (
        <React.Fragment>
            <div id="background"/>
            <TitleContainer className='personal' title={person.name} desc={person.desc}>
                <PhotoContainer nick={nick}/>
            </TitleContainer>

            {person.projects.map((x, i) => (<CardType cardId={"card" + (i + 1)} last={i === person.projects.length - 1} rtl={i % 2 === 0}>
                <ProjGallery person={nick} project={x}/>
            </CardType>))}

            <Footer/>
        </React.Fragment>
    )
};

export default Personal;