import React from "react";
import './IconContainer.css';

export const IconsContainer = ({names}) => {
    const renderedImages = names.map(name => (
        <img src={require('../../../icons/' + name + '.svg').default} alt={name} className="icon"/>
    ))

    return (
        <div className="icons-container">
            {renderedImages}
        </div>
    )
}